.profile-container {
  width: 100%;
  display: flex;
  overflow: auto;
  min-height: 100vh;
  align-items: center;
  flex-direction: column;
  background-size: cover;
  background-image: linear-gradient(180deg, rgba(218, 223, 227, 0.1) 0.00%,rgba(49, 42, 16, 0.89) 82.00%),url("/playground_assets/wp8884108-1500w.webp");
  background-repeat: no-repeat;
  background-position: left;
}
.profile-header {
  width: 100%;
  height: 113px;
  display: flex;
  position: relative;
  max-width: 100%;
  align-items: center;
  padding-top: var(--dl-space-space-twounits);
  padding-left: var(--dl-space-space-threeunits);
  padding-right: var(--dl-space-space-threeunits);
  padding-bottom: 21px;
  justify-content: flex-start;
  background-image: linear-gradient(0deg, rgba(189, 195, 199, 0) 12.00%,rgba(0, 0, 0, 0.68) 66.00%);
}
.profile-nav {
  top: 24px;
  flex: 0 0 auto;
  left: 67px;
  width: 537px;
  height: 66px;
  display: flex;
  position: absolute;
  align-self: stretch;
  align-items: flex-start;
  flex-direction: row;
  justify-content: flex-start;
}
.profile-navlink {
  display: contents;
}
.profile-image {
  width: 170px;
  height: 93px;
  object-fit: cover;
  text-decoration: none;
}
.profile-attitude {
  color: #d4d4d4;
  font-size: 20px;
  margin-top: var(--dl-space-space-oneandhalfunits);
  transition: 0.3s;
  font-family: Oxygen;
  margin-left: var(--dl-space-space-threeunits);
  margin-right: 50px;
}
.profile-attitude:hover {
  cursor: pointer;
}
.profile-rules {
  color: #d4d4d4;
  font-size: 20px;
  margin-top: var(--dl-space-space-oneandhalfunits);
  transition: 0.3s;
  font-family: Oxygen;
}
.profile-rules:hover {
  cursor: pointer;
}
.profile-about-us {
  color: #d4d4d4;
  font-size: 20px;
  margin-top: var(--dl-space-space-oneandhalfunits);
  transition: 0.3s;
  font-family: Oxygen;
  margin-left: 50px;
  margin-bottom: 0px;
}
.profile-about-us:hover {
  cursor: pointer;
}
